import { Component, h, Prop, Element, State, Method } from '@stencil/core';
import { common } from '../../global/data/common';
import InputInterface from './inputInterface';
@Component({
      tag: 'ks-input-check',
      styleUrl: 'inputCheck.css'
})
export class InputCheck implements InputInterface {

    @Prop() name: string;
    @Prop() label: string;
    @Prop() value: string;

    @Prop({reflect: true}) large: boolean;

    @Prop() radio: boolean;
    @Prop() checkbox: boolean;

    @Prop() checked: boolean = false;

    @Prop() nomessage: boolean = false;
    @Prop() required: boolean;

    @Element() root: HTMLElement;

    @Prop({reflect: true, mutable: true}) invalid: boolean = false;
    @State() invalidMessage: string = "";

	render() {
        const type = 
            this.radio ? "radio" :
            this.checkbox ? "checkbox" :
            "checkbox";

		return[
            <label>
                <input name={this.name} type={type} checked={this.checked} value={this.value}
                    onChange={()=>this.Validate()} />

                {type == "checkbox" ?
                    <ks-icon name="check" size={this.large ? 1.0 : 0.64}/>
                : null}

                <span innerHTML={this.label}></span>
            </label>,

            this.invalid && this.required && !this.nomessage ? 
                <p>{this.invalidMessage}</p>
            : null
        ];
    }

    @Method()
    async IsValid() {
        return !this.invalid;
    }

    @Method()
    async Validate() {
        const strings = common.get("translations");
        const input = this.root.querySelector("input");

        let valid = true;
        let message = "";

        if(this.required && !input.checked) {
            valid = false;
            message = strings.inputFieldRequired;
        }

        this.invalid = !valid;
        this.invalidMessage = message;

        return Promise.resolve();
    }
}
