import { Component, h, Prop, Element, Method, Watch } from '@stencil/core';
import { common } from '../../global/data/common';
import InputInterface from './inputInterface';
@Component({
      tag: 'ks-input-text',
      styleUrl: 'inputText.css'
    })
export class InputText implements InputInterface {
    @Element() root: HTMLElement;
        
    @Prop() name: string;
    @Prop() placeholder: string;
    @Prop() label: string;
    @Prop() icon: string;
    @Prop() disabled: boolean;

    @Prop({reflect: true, mutable: true}) value: string;

    @Prop() digits: boolean;
    @Prop() price: boolean;
    @Prop() email: boolean;
    @Prop() url: boolean;
    @Prop() password: boolean;
    @Prop() sameAs: string = "";
    
    @Prop() required: boolean;

    @Prop() regex: string = "";
    @Prop() regexMessage: string = "";

    @Prop() min: number;
    @Prop() max: number;

    @Prop({reflect: true}) center: boolean;
    @Prop({reflect: true}) large: boolean;
    @Prop({reflect: true}) emphasis: boolean;

    @Prop() nomessage: boolean = false;
    @Prop({mutable: true, reflect: true}) novalidate: boolean = false;
    @Prop({mutable: true, reflect: true}) message: string = "";
    @Prop({mutable: true, reflect: true}) invalid: boolean = false;

	render() {
        const type = this.email ? "email" : 
                     this.url ? "url" :
                     this.password ? "password" :
                     "text";

		return[
            this.label ?
                <label>{this.label}</label>
            : null,

            !!this.icon ?
                <div class="wrapper">
                    <ks-icon name={this.icon} size={0.95}/>
                    <input name={this.name} type={type} value={this.value}
                           placeholder={this.placeholder} onChange={()=>this.Change()}/>
                </div>
            : 
                <input name={this.name} type={type} value={this.value} disabled={this.disabled}
                       placeholder={this.placeholder}
                       onChange={()=>this.Change()}
                />
            ,
            
            this.invalid && !this.nomessage ?
                <p>{this.message}</p>
            : null
        ];
    }

    Change() {
        this.value = this.root.querySelector("input").value;

        if(!this.novalidate) this.Validate();
    }

    @Watch("message")
    MessageWatcher() {
        this.invalid = this.message != "";
    }

    @Method()
    async IsValid() {
        return this.message == "" ? true : false;
    }

    @Method()
    async Validate() {
        const strings = common.get("translations");

        const input = this.root.querySelector("input");
        let message = [];

        if(this.email) {
            const regex = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
            
            if(!regex.test(input.value.toLowerCase()))
                message = [strings.inputIncorrectEmail];
        }
        
        if(this.digits) {
            const regex = /^([0-9])*$/;
            if(!regex.test(input.value))
                message = [strings.inputOnlyNumbers];
        }

        if(this.price) {
            input.value = input.value.replace(",", ".");
            if(!isNaN(parseFloat(input.value)))
                input.value = parseFloat(input.value).toFixed(2);

            const regex = /^([0-9])*([.][0-9]{1,2})?$/;
            if(!regex.test(input.value))
                message = [strings.inputOnlyPricesWithoutCurrency];
        }

        if(this.sameAs) {
            const other = this.root.closest("form").querySelector(`input[name='${this.sameAs}']`) as HTMLInputElement;

            if(input.value != other.value)
                message.push(this.password ? strings.inputPasswordsDontMatch : strings.inputFieldsDontMatch);
        }

        if(this.regex != "" && this.regexMessage != "") {
            const regex = new RegExp(this.regex, "i");

            if(!regex.test(input.value))
                message.push(this.regexMessage);
        }

        if(input.value.length < this.min)
            message.push(strings.inputMinCharacters.replace('{count}', this.min.toString()));

        if(input.value.length > this.max)
            message.push(strings.inputMaxCharacters.replace('{count}', this.max.toString()));

        if(this.required && input.value.length == 0)
            message = [strings.inputFieldRequired];

        this.message = message.join(" ");

        return Promise.resolve();
    }
}
